.timeline {
  width: 100%;
  margin: 40px auto 20px auto;
  border-bottom: 1px solid #000;
  padding-bottom: 100px;
  overflow: hidden;
  .timeline_wrap {
    height: 900px;
    position: relative;
    li {
      background: url(../../images/timeline1.jpg) no-repeat;
      background-position: center top;
      position: absolute;
      width: 70%;
      height: 900px;
      right: 3%;
      z-index: 1;
      .box {
        position: relative;
        height: 900px;
        .timeline_text {
          position: absolute;
          left: -30%;
          top: 10%;
          height: 30%;
          width: 300px;
          .timeline_contents {
            display: inline-block;
            font-family: "Centra No2", "Noto Sans KR";
            font-size: 60px;
            line-height: 80px;
            width: 393px;
            font-weight: 400;
            letter-spacing: -3px;
          }
          .timeline_title {
            display: inline-block;
            font-family: "Centra No2", "Noto Sans KR";
            font-size: 80px;
            line-height: 104px;
            font-weight: bold;
            width: 465px;
            margin-top: 10px;
          }
        }
      }
      .timeline_outlink {
        position: absolute;
        left: -30%;
        top: 40%;
        height: 100px;
        width: 280px;
        margin-top: 40px;
        a {
          position: relative;
          width: 100%;
          .outlink_img {
            display: inline-block;
            position: absolute;
            span {
              background: url(../../images/outlinkPlay.svg) no-repeat;
              width: 28px;
              height: 28px;
              position: absolute;
              left: 72px;
            }
            img {
              width: 100px;
              height: 100px;
              border-radius: 50%;
            }
          }
          .outlink_text {
            display: inline-block;
            font-size: 14px;
            font-weight: bold;
            color: #999;
            line-height: 21px;
            position: absolute;
            left: 40%;
            margin-top: 30px;
            word-break: keep-all;
            &:hover {
              color: #000;
            }
          }
        }
      }
    }
  }
  .timeline_bar {
    position: absolute;
    top: 80%;
    z-index: 999;
    #bar_prev {
      position: absolute;
      top: 100px;
      left: 110px;
      .hand {
        display: inline-block;
        background: url(../../images/prevBr.png) no-repeat;
        width: 75px;
        height: 26px;
        cursor: pointer;
        &:hover {
          background: url(../../images/prevBl.png) no-repeat;
        }
      }
    }
    #bar_next {
      position: absolute;
      top: 100px;
      left: 210px;
      .hand {
        display: inline-block;
        background: url(../../images/nextBr.png) no-repeat;
        width: 78px;
        height: 26px;
        cursor: pointer;
        &:hover {
          background: url(../../images/nextBl.png) no-repeat;
        }
      }
    }
  }
}
