.gnb2 {
  height: 60px;
  border-bottom: 1px solid #bcbcbc;
  display: flex;
  justify-content: space-between;
  font-family: "Centra No2", sans-serif;
  position: relative;
  a {
    padding: 15px 0;
    img {
      width: 80px;
      height: 24px;
      vertical-align: middle;
      margin-left: 120px;
    }
  }
  nav {
    margin-right: 40px;
  }
  .menu {
    display: flex;
    font-family: "Centra No2", sans-serif;
    font-weight: bold;
    li {
      width: 165px;
      font-size: 16px;
      padding-top: 5px;
      a {
        &:hover {
          color: blue;
        }
      }
      &:hover .sub_menu1 {
        display: block;
      }
      &:hover .sub_menu2 {
        display: block;
      }
      &:hover .sub_menu3 {
        display: block;
      }
      .sub_menu1 {
        width: 165px;
        background-color: #282828;
        color: #fff;
        display: none;
        position: absolute;
        top: 100%;
        left: 70%;
        z-index: 2;
        .last {
          margin-bottom: 20px;
        }
        li {
          font-size: 14px;
          width: 100%;
          height: 40px;
          font-family: "Centra No2", "Noto Sans KR";
          font-weight: normal;
          padding: 20px 30px;
          a {
            height: 45px;
            padding: 0;
            &:hover {
              color: blue;
            }
          }
          span {
            display: inline-block;
            font-size: 15px;
            font-weight: bold;
            margin-top: 10px;
            &:hover {
              color: #fff;
            }
          }
        }
      }
      .sub_menu2 {
        margin: 30px 40px 30px 30px;
        height: 480px;
        width: 720px;
        background-color: #282828;
        color: #fff;
        position: absolute;
        top: 50%;
        right: -2%;
        padding: 40px;
        display: none;
        z-index: 2;
        .first {
          width: 171px;
          &:hover {
            color: #fff;
          }
        }
        dt {
          float: left;
          clear: left;
          height: 18px;
          border-right: 1px solid #444;
          padding: 0 20px 32px 0;
          margin-right: 20px;
          &:hover {
            color: blue;
          }
          span {
            font-size: 15px;
            font-weight: bold;
            line-height: 19px;
            width: 150px;
            display: inline-block;
          }
        }
        dd {
          float: left;
          height: 18px;
          padding: 0 0 24px 15px;
          &:hover {
            color: blue;
          }
          span {
            font-size: 14px;
            font-weight: normal;
            line-height: 18px;
            display: inline-block;
          }
        }
        a {
          padding: 0;
        }
      }
      .sub_menu3 {
        display: none;
        width: 195px;
        height: 458px;
        background-color: #282828;
        color: #fff;
        margin: 30px;
        position: absolute;
        top: 50%;
        right: 0;
        z-index: 2;
        li {
          font-size: 14px;
          font-weight: normal;
          height: 18px;
          padding: 18px 0 20px 30px;
        }
      }
    }
  }
}
