.interior {
  padding: 80px 0 120px 0;
  padding-bottom: 100px;
  .interior_inner {
    width: 1220px;
    margin: 0 auto;
    .interior_title {
      height: 44px;
      margin-bottom: 40px;
      h2 {
        line-height: 31px;
        font-size: 24px;
      }
      a {
        color: #002bd2;
        font-size: 13px;
        float: right;
        margin-right: 10px;
        font-family: "Centra No2", "Noto Sans KR";
        &:hover {
          text-decoration: underline;
        }
      }
    }
    .interior_list {
      .fit {
        display: inline-block;
        margin-right: 100px;
        width: 340px;
        box-sizing: content-box;
        font-family: "Centra No2", "Noto Sans KR";
        text-align: center;
        &:last-child {
          margin-right: 0;
        }
        a {
          img {
            width: 100%;
            height: 450px;
            &:hover {
              filter: opacity(70%);
            }
          }
        }
        h3 {
          margin-top: 30px;
          line-height: 52px;
          font-size: 40px;
          font-weight: bold;
        }
        p {
          margin-top: 10px;
          line-height: 27px;
          font-size: 18px;
        }
      }
    }
  }
}
