.notice {
  background-color: #f5f5f5;
  padding-top: 80px;
  padding-bottom: 120px;
  .notice_inner {
    min-height: 635px;
    width: 1220px;
    margin: 0 auto;
    .news {
      height: 60px;
      width: 100%;
      margin-bottom: 80px;
      span:first-child {
        display: inline-block;
        float: left;
        background-color: #002bd2;
        width: 12%;
        height: 60px;
        color: #fff;
        font-family: "Centra No2", "Noto Sans KR";
        font-size: 18px;
        font-weight: bold;
        line-height: 23px;
        padding-top: 18px;
        text-align: center;
      }
      span:last-child {
        display: inline-block;
        float: left;
        height: 60px;
        width: 88%;
        background-color: #dde6ff;
        div {
          padding: 1.9% 1.5% 0 25%;
          font-size: 14px;
          display: flex;
          .content {
            font-weight: bold;
            width: 220px;
            &:hover {
              text-decoration: underline;
            }
          }
          span {
            font-family: "Centra No2", "Noto Sans KR";
            margin-left: 10px;
            margin-top: 2px;
            color: #555;
          }
          .more {
            font-size: 12px;
            line-height: 18px;
            text-decoration: underline;
            margin-left: 440px;
            color: #555;
            &:hover {
              color: #002bd2;
            }
          }
        }
      }
    }
    .instagram {
      height: 308px;
      margin-bottom: 100px;
      width: 100%;
      a {
        div {
          margin-bottom: 30px;
          height: 55px;
          .instagram_logo {
            width: 50px;
            height: 50px;
            background: url(../../images/pc_insta_profile.png) no-repeat;
            background-size: cover;
            margin-left: 15px;
            float: left;
          }
          .instagram_id {
            float: left;
            margin-top: 10px;
            margin-left: 10px;
            font-size: 20px;
            font-family: "Centra No2", "Noto Sans KR";
          }
        }
      }
      .instagram_contents {
        display: flex;
        justify-content: space-between;
        margin-right: 20px;
        li a img {
          height: 228px;
          width: 228px;
        }
      }
    }
    .blog {
      height: 350px;
      width: 100%;
      .blog_text {
        width: 390px;
        height: 350px;
        float: left;
        div {
          margin-bottom: 30px;
          height: 30px;
          .blog_icon {
            background: url(../../images/blog.png) no-repeat;
            float: left;
            margin-right: 15px;
            height: 30px;
            width: 30px;
          }
          .txt {
            line-height: 23px;
            float: left;
            font-size: 18px;
          }
        }
        .blog_title {
          width: 90%;
          font-size: 32px;
          font-weight: bold;
          margin-bottom: 20px;
          word-break: keep-all;
          font-family: "Centra No2", "Noto Sans KR";
          float: left;
        }
        .blog_content {
          width: 390px;
          font-size: 15px;
          font-weight: 400;
          color: #555;
          margin-bottom: 30px;
          word-break: keep-all;
          font-family: "Centra No2", "Noto Sans KR";
          float: left;
        }
        a {
          font-family: "Centra No2", "Noto Sans KR";
          text-decoration: underline;
          color: #002bd2;
          float: left;
          font-size: 13px;
        }
      }
      .blog_link {
        float: right;
        width: 620px;
        height: 350px;
        background: url(../../images/blogMainImg2.jpg) no-repeat;
      }
    }
  }
}
