.go {
  img {
    vertical-align: middle;
  }
  .find_store {
    position: fixed;
    bottom: 84px;
    right: 40px;
    z-index: 990;
    width: 99px;
    height: 36px;
    background-color: #fff;
    border-radius: 100px;
    box-shadow: 0px 4px 5px rgb(0 0 0 / 15%);
    cursor: pointer;
    span {
      display: inline-block;
      padding: 8px 16px;
      font-size: 13px;
      line-height: 19px;
    }
  }
  .kakao_chat {
    position: fixed;
    bottom: 40px;
    right: 40px;
    z-index: 990;
    width: 99px;
    height: 36px;
    background-color: #f9e000;
    border-radius: 100px;
    box-shadow: 0px 4px 5px rgb(0 0 0 / 15%);
    cursor: pointer;
    span {
      display: inline-block;
      padding: 8px 16px;
      font-size: 13px;
      line-height: 19px;
    }
  }
}
