footer {
  width: 100%;
  background-color: #141414;
  color: #fff;
  .footer_inner {
    padding-top: 60px;
    width: 1220px;
    height: 610px;
    margin: 0 auto;
    position: relative;
    .footer_box1 {
      height: 44px;
      margin-bottom: 40px;
      .logo_new {
        height: 45px;
        float: left;
      }
      .snslogo_list {
        margin-top: 10px;
        float: right;
        width: 150px;
        height: 45px;
        .fb {
          background: url(../../images/facebookIcon.svg) no-repeat;
        }
        .insta {
          background: url(../../images/instaIcon.svg) no-repeat;
        }
        .blog {
          background: url(../../images/blogIcon.svg) no-repeat;
        }
        .snslogo {
          width: 30px;
          height: 30px;
          margin-left: 20px;
          float: left;
        }
      }
    }
    .footer_box2 {
      margin-bottom: 120px;
      .footer_left {
        .company {
          font-size: 15px;
          font-weight: 700;
          padding: 16px 0;
        }
        .copyright {
          font-size: 14px;
        }
        p {
          font-size: 13px;
        }
        .spclogo {
          display: inline-block;
          background: url(../../images/spclogo.png) no-repeat;
          width: 60px;
          height: 40px;
        }
        span {
          font-size: 13px;
          font-weight: 700;
        }
      }
      .footer_right {
        width: 80%;
        position: absolute;
        left: 30%;
        top: 24%;
        display: flex;
        flex-wrap: wrap;
        .h55 {
          .subUl {
            display: flex;
            margin-bottom: 20px;
            li {
              margin-right: 74px;
              a {
                font-family: "Centra No2", "Noto Sans KR";
                font-weight: 400;
                font-size: 13px;
                text-decoration: underline;
                padding-bottom: 20px;
              }
              .color {
                color: #1a59ff;
              }
            }
          }
        }
        .menu {
          width: 100%;
          .top {
            margin-top: 90px;
            p {
              font-size: 13px;
              font-weight: 700;
              text-decoration: underline;
              margin-top: 20px;
            }
          }
          .menu_title {
            float: left;
            font-size: 15px;
            font-weight: bold;
            line-height: 22px;
            height: 44px;
            margin-right: 67px;
            font-family: "Centra No2", "Noto Sans KR";
            .tel {
              font-size: 24px;
              font-weight: 400;
              margin-top: 20px;
              line-height: 31px;
            }
            .time {
              line-height: 24px;
              margin-top: 20px;
              font-size: 12px;
              strong {
                font-weight: bold;
              }
              span {
                color: #999;
                font-size: 12px;
                font-family: "Malgun Gothic", "Apple SD Gothic Neo", Dotum;
              }
            }
            ul {
              margin-top: 20px;
              li {
                a {
                  font-weight: normal;
                  line-height: 30px;
                  font-size: 13px;
                }
              }
            }
          }
          .left {
            margin-left: 8px;
          }
        }
      }
    }
  }
}
