.task {
  padding-top: 80px;
  border-bottom: 1px solid #cccccc;
  padding-bottom: 60px;
  .task_inner {
    width: 1220px;
    margin: 0 auto;
    .task_title {
      height: 44px;
      h2 {
        line-height: 44px;
        font-size: 34px;
        font-weight: normal;
        width: 190px;
        float: left;
        margin-left: 30px;
      }
      a {
        color: #002bd2;
        font-size: 13px;
        float: right;
        margin-right: 30px;
        margin-top: 20px;
        font-family: "Centra No2", "Noto Sans KR";
        &:hover {
          text-decoration: underline;
        }
      }
    }
    .task_list {
      width: 100%;
      display: flex;
      justify-content: space-between;
      .fit {
        text-align: center;
        padding: 40px;
        font-family: "Centra No2", "Noto Sans KR";
        img:hover {
          filter: opacity(70%);
        }
        h3 {
          font-size: 50px;
          font-weight: bold;
          padding-top: 20px;
        }
        p {
          font-size: 24px;
        }
      }
    }
  }
}
